.education {
  background: var(--background-color);
}

.education-content {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.subsection-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: var(--text-primary);
  text-align: center;
  position: relative;
}

.subsection-title::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.education-timeline {
  position: relative;
  padding-left: 2rem;
}

.education-timeline::before {
  content: '';
  position: absolute;
  left: 1rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--gradient-primary);
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
}

.timeline-marker {
  position: absolute;
  left: -1.5rem;
  top: 0;
}

.timeline-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  box-shadow: var(--shadow-medium);
}

.timeline-content {
  margin-left: 1rem;
}

.education-card {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.education-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-large);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  gap: 1rem;
}

.degree-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  flex: 1;
}

.status {
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status.completed {
  background: #dcfce7;
  color: #166534;
}

.status.in-progress {
  background: #dbeafe;
  color: #1e40af;
}

.institution-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.95rem;
  color: var(--text-secondary);
}

.info-icon {
  color: var(--primary-color);
  font-size: 0.9rem;
}

.education-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
}

.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.certification-card {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
  text-align: center;
  transition: all 0.3s ease;
}

.certification-card:hover {
  box-shadow: var(--shadow-large);
}

.cert-icon {
  width: 4rem;
  height: 4rem;
  margin: 0 auto 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  color: white;
  border-radius: 50%;
  font-size: 1.5rem;
}

.cert-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.cert-issuer {
  font-weight: 500;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.cert-location {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

.location-icon {
  font-size: 0.8rem;
}

.cert-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.cert-link:hover {
  color: var(--primary-dark);
}

/* Mobile Styles */
@media (max-width: 768px) {
  .education-content {
    gap: 3rem;
  }

  .education-timeline {
    padding-left: 1.5rem;
  }

  .education-timeline::before {
    left: 0.75rem;
  }

  .timeline-marker {
    left: -1.25rem;
  }

  .timeline-icon {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
  }

  .education-card {
    padding: 1.5rem;
  }

  .card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .degree-title {
    font-size: 1.1rem;
  }

  .certifications-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .certification-card {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .education-timeline {
    padding-left: 1rem;
  }

  .timeline-marker {
    left: -1rem;
  }

  .timeline-icon {
    width: 2rem;
    height: 2rem;
    font-size: 0.9rem;
  }

  .education-card {
    padding: 1rem;
  }

  .certification-card {
    padding: 1rem;
  }

  .cert-icon {
    width: 3rem;
    height: 3rem;
    font-size: 1.2rem;
  }
}
