.publications {
  background: var(--background-secondary);
}

.publications-content {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.publications-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.publication-card {
  background: white;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
  overflow: hidden;
  transition: all 0.3s ease;
}

.publication-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-large);
}

.publication-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 2rem;
  background: var(--background-secondary);
  border-bottom: 1px solid var(--border-color);
}

.pub-icon {
  width: 3rem;
  height: 3rem;
  background: var(--gradient-primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
}

.pub-meta {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex-wrap: wrap;
}

.pub-type,
.pub-year {
  background: var(--background-color);
  color: var(--text-secondary);
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid var(--border-color);
}

.pub-status {
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.pub-status.published {
  background: #dcfce7;
  color: #166534;
}

.pub-status.under-review {
  background: #fef3c7;
  color: #92400e;
}

.publication-content {
  padding: 2rem;
}

.pub-title {
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  line-height: 1.4;
}

.pub-authors,
.pub-venue {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  font-size: 0.95rem;
  color: var(--text-secondary);
}

.authors-icon,
.venue-icon {
  color: var(--primary-color);
  font-size: 0.9rem;
}

.pub-abstract {
  margin: 1.5rem 0;
  padding: 1.5rem;
  background: var(--background-secondary);
  border-radius: 0.5rem;
  border-left: 4px solid var(--primary-color);
  position: relative;
}

.quote-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: var(--primary-color);
  opacity: 0.3;
  font-size: 1.2rem;
}

.pub-abstract p {
  margin: 0;
  line-height: 1.6;
  color: var(--text-secondary);
  font-style: italic;
}

.cv-link-note {
  margin-top: 1rem;
  padding: 0.75rem;
  background: #e3f2fd;
  border: 1px solid #2196f3;
  border-radius: 0.5rem;
  text-align: center;
}

.cv-link-note small {
  color: #1976d2;
  font-weight: 500;
  font-style: normal;
}

.pub-keywords {
  margin: 1.5rem 0;
}

.pub-keywords h4,
.pub-citation h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
}

.keywords-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.keyword-tag {
  background: var(--primary-color);
  color: white;
  padding: 0.3rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  font-weight: 500;
}

.pub-citation {
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

.citation-text {
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: var(--text-secondary);
  background: var(--background-secondary);
  padding: 1rem;
  border-radius: 0.5rem;
  line-height: 1.5;
  margin: 0;
}

.publication-actions {
  display: flex;
  gap: 1rem;
  padding: 1.5rem 2rem 2rem;
  border-top: 1px solid var(--border-color);
}

.pub-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem;
  font-size: 0.9rem;
}

.research-interests {
  background: white;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
}

.interests-card {
  padding: 2.5rem;
  text-align: center;
}

.interests-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.interests-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.interests-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.interest-item {
  background: var(--background-secondary);
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.interest-item:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
}

.research-cta {
  background: white;
  border-radius: 1rem;
  box-shadow: var(--shadow-medium);
  border: 1px solid var(--border-color);
}

.cta-card {
  padding: 3rem;
  text-align: center;
}

.cta-card h3 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.cta-card p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .publication-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
    padding: 1.5rem;
  }

  .pub-meta {
    justify-content: center;
  }

  .publication-content {
    padding: 1.5rem;
  }

  .pub-title {
    font-size: 1.2rem;
  }

  .publication-actions {
    flex-direction: column;
    padding: 1rem 1.5rem 1.5rem;
  }

  .pub-btn {
    width: 100%;
  }

  .interests-card,
  .cta-card {
    padding: 2rem;
  }

  .interests-grid {
    grid-template-columns: 1fr;
  }

  .cta-actions {
    flex-direction: column;
    align-items: center;
  }

  .cta-actions .btn {
    width: 100%;
    max-width: 250px;
  }
}

@media (max-width: 480px) {
  .publication-content {
    padding: 1rem;
  }

  .pub-abstract {
    padding: 1rem;
  }

  .citation-text {
    font-size: 0.8rem;
    padding: 0.75rem;
  }

  .interests-card,
  .cta-card {
    padding: 1.5rem;
  }

  .cta-card h3 {
    font-size: 1.5rem;
  }

  .cta-card p {
    font-size: 1rem;
  }
}
