.about {
  background: var(--background-secondary);
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.about-text {
  background: white;
  padding: 2.5rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
}

.about-description p {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-secondary);
}

.about-description p:last-child {
  margin-bottom: 0;
}

.about-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.personal-info,
.languages {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
}

.personal-info h3,
.languages h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.info-grid {
  display: grid;
  gap: 1rem;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  border-radius: 0.5rem;
  background: var(--background-secondary);
  transition: all 0.3s ease;
}

.info-item:hover {
  background: var(--primary-color);
  color: white;
  transform: translateX(5px);
}

.info-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: var(--primary-color);
  color: white;
  border-radius: 50%;
  font-size: 1rem;
  flex-shrink: 0;
}

.info-item:hover .info-icon {
  background: white;
  color: var(--primary-color);
}

.info-content {
  display: flex;
  flex-direction: column;
}

.info-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.info-item:hover .info-label {
  color: rgba(255, 255, 255, 0.8);
}

.info-value {
  font-weight: 600;
  color: var(--text-primary);
}

.info-item:hover .info-value {
  color: white;
}

.language-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.language-item {
  padding: 1rem;
  border-radius: 0.5rem;
  background: var(--background-secondary);
}

.language-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.language-name {
  font-weight: 600;
  color: var(--text-primary);
}

.language-level {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.language-bar {
  height: 6px;
  background: var(--border-color);
  border-radius: 3px;
  overflow: hidden;
}

.language-progress {
  height: 100%;
  background: var(--gradient-primary);
  border-radius: 3px;
  transition: width 1s ease;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .about-text,
  .personal-info,
  .languages {
    padding: 1.5rem;
  }

  .about-description p {
    font-size: 1rem;
  }

  .info-grid {
    gap: 0.75rem;
  }

  .info-item {
    padding: 0.5rem;
  }

  .info-icon {
    width: 35px;
    height: 35px;
    font-size: 0.9rem;
  }

  .language-item {
    padding: 0.75rem;
  }
}

@media (max-width: 480px) {
  .about-text,
  .personal-info,
  .languages {
    padding: 1rem;
  }

  .language-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
}
