.contact {
  background: var(--background-color);
}

.contact-intro {
  text-align: center;
  margin-bottom: 3rem;
}

.contact-intro p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 700px;
  margin: 0 auto;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.contact-info-section h3,
.contact-form-section h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: var(--text-primary);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.contact-cards {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.contact-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: white;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
}

.contact-card:hover {
  box-shadow: var(--shadow-medium);
  transform: translateY(-2px);
}

.contact-icon {
  width: 3rem;
  height: 3rem;
  background: var(--gradient-primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  flex-shrink: 0;
}

.contact-details h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.contact-value {
  font-weight: 500;
  color: var(--primary-color);
  margin-bottom: 0.25rem;
}

.contact-description {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin: 0;
}

.availability {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
}

.availability h4 {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.availability p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1rem;
}

.availability ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.availability li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--text-secondary);
}

.availability li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--primary-color);
  font-weight: 600;
}

.contact-form {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.form-icon {
  color: var(--primary-color);
  font-size: 0.9rem;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1rem;
  border: 2px solid var(--border-color);
  border-radius: 0.5rem;
  font-family: inherit;
  font-size: 1rem;
  transition: all 0.3s ease;
  resize: vertical;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-secondary);
}

.submit-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  margin-top: 1rem;
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.form-message {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  text-align: center;
}

.form-message.success {
  background: #dcfce7;
  color: #166534;
  border: 1px solid #bbf7d0;
}

.form-message.error {
  background: #fef2f2;
  color: #dc2626;
  border: 1px solid #fecaca;
}

.form-message p {
  margin: 0;
  font-weight: 500;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .contact-intro p {
    font-size: 1.1rem;
  }

  .contact-card {
    padding: 1rem;
  }

  .contact-icon {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
  }

  .contact-details h4 {
    font-size: 1rem;
  }

  .contact-form {
    padding: 1.5rem;
  }

  .availability {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .contact-intro {
    margin-bottom: 2rem;
  }

  .contact-intro p {
    font-size: 1rem;
  }

  .contact-card {
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
  }

  .contact-details h4 {
    margin-top: 0.5rem;
  }

  .contact-form {
    padding: 1rem;
  }

  .form-group input,
  .form-group textarea {
    padding: 0.75rem;
  }

  .availability {
    padding: 1rem;
  }

  .submit-btn {
    padding: 0.875rem;
    font-size: 1rem;
  }
}
