.skills {
  background: var(--background-secondary);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.skill-category {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-light);
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-large);
}

.category-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid var(--border-color);
}

.category-icon {
  width: 3rem;
  height: 3rem;
  background: var(--gradient-primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
}

.category-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.skill-item {
  padding: 1rem;
  border-radius: 0.5rem;
  background: var(--background-secondary);
  transition: all 0.3s ease;
}

.skill-item:hover {
  background: rgba(37, 99, 235, 0.05);
  transform: translateX(5px);
}

.skill-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.skill-name {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  color: var(--text-primary);
}

.skill-icon {
  font-size: 1.1rem;
}

.skill-percentage {
  font-weight: 600;
  color: var(--primary-color);
  font-size: 0.9rem;
}

.skill-bar {
  height: 6px;
  background: var(--border-color);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  border-radius: 3px;
  transition: width 1.2s ease;
  position: relative;
}

.skill-progress::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 20px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3));
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.skills-summary {
  margin-top: 3rem;
}

.summary-card {
  background: white;
  padding: 3rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-medium);
  border: 1px solid var(--border-color);
  text-align: center;
}

.summary-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.summary-card p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.expertise-highlights {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-top: 2rem;
}

.highlight {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: var(--background-secondary);
}

.highlight-number {
  font-size: 2rem;
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.highlight-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .skill-category {
    padding: 1.5rem;
  }

  .category-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .category-icon {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.1rem;
  }

  .category-title {
    font-size: 1.1rem;
  }

  .skill-item {
    padding: 0.75rem;
  }

  .summary-card {
    padding: 2rem;
  }

  .expertise-highlights {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .highlight {
    padding: 1rem;
  }

  .highlight-number {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }

  .skill-category {
    padding: 1rem;
  }

  .summary-card {
    padding: 1.5rem;
  }

  .summary-card p {
    font-size: 1rem;
  }

  .skill-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }

  .skill-percentage {
    align-self: flex-end;
  }
}
